@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    @include m.contenedor;
    &-imagenes{
        @include m.grid(1,2rem);

        @include m.min-sm{
            @include m.grid(2,2rem);
        }
        @include m.min-md{
            @include m.grid(3,2rem);
        }
        @include m.min-lg{
            @include m.grid(4,2rem);
        }
        @include m.min-xl{
            @include m.grid(4,2rem);
        }
        img{
            height: 20rem;
            object-fit: cover;
            transition: transform 0.2s ease-in-out, filter 0.2s ease-in-out;  
            @include m.min-sm{
                &:hover{
                    transform: scale(1.1);
                    cursor: pointer;
                    filter: brightness(.8);
                }
            }
        }
    }
}

.modal{
    position: fixed;
    inset: 0;
    background-color: rgba(v.$negro, 0.5);
    display: grid;
    place-items: center;
    animation: fadeIn ease-in 0.3s;

    &.fadeOut{
        animation: fadeOut ease-in 0.3s;
    }

    img{
        max-width: 68rem;
    }
}   

.btn-cerrar{
    transition: backgroud-color, color, ease-in-out 0.1s;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    border: none;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;
    cursor: pointer;
    &:hover{
        background-color: v.$rosa;
        color: v.$blanco;
    }
}

@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes fadeOut{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}